<!-- 111 -->
<template>
	<view class="pages">
		<!-- 头部=banner -->
		<view class="bannercontentbox">
			<view class="bannercontent">
				<swiper class="swiper" :indicator-dots="true" :autoplay="true" :duration="700" :circular="true"
					indicator-active-color="#FFFFFF" indicator-color="rgba(255, 255, 255, 0.58)">
					<swiper-item v-for="(item,index) in list" :key="index">
						<image :src="item.image" class="itemimg"></image>
					</swiper-item>
				</swiper>
			</view>
		</view>

		<view class="titlecontent">
			<text>案例详情</text>
			<text>更多 ></text>
		</view>
		<view class="content">
			<view class="data divflex">
				<view @click="goNearbysun(item)" v-for="(item,index) in inforLists" :key="index" class="list">
					<view class="imgsbox">
						<image class="listimgs" :src="item.image" mode="aspectFit"></image>
					</view>
					<view class="list-rights">
						<view class="list-rightstitle">{{item.name}}</view>
						<view class="list-rightscontent">{{item.description}}</view>
					</view>

				</view>
			</view>
		</view>
		<page-tabpars></page-tabpars>
	</view>
</template>
<script>
	import {
		mapGetters,
		mapMutations
	} from 'vuex'
	import {
		inforMations
	} from "@/api/api.js"
	export default {
		data() {
			return {
				list: [{
						image: 'data:image/jpeg;base64,',

					},
					{
						image: 'data:image/jpeg;base64,',

					},
					{
						image: 'data:image/jpeg;base64,',

					},
					{
						image: 'data:image/jpeg;base64,',

					}
				],
				current: 0,
				mode: 'round',
				inforLists: [],

			};
		},
		computed: {
			...mapGetters(['active']),
		},
		created() {},

		onLoad() {
			this.inforList();
		},
		onShow() {
			const token = uni.getStorageSync('token')
			// console.log(token,"token值")
			if (!token) {
				uni.navigateTo({
					url: '../login/index'
				})
				return
			}
		},




		methods: {
			...mapMutations(['SET_ACTIVE']),

			// 获取资讯 列表
			inforList() {
				let that = this
				let account = uni.getStorageSync('account')
				inforMations({
					account
				}).then(res => {
					// console.log(res,"1")
					that.inforLists = res
					that.inforLists = that.inforLists.map(item => {
						item.image = item.image.replace(/\\/g, '/')
						item.image = this.platformurl + item.image
						return item
					})
				})
			},

			// 跳转详情
			goNearbysun(item) {
				console.log(item, 'res')
				let title = item.name
				let keyValue = item.id
				// return
				uni.navigateTo({
					url: `/pages/nearbysun/index?title=${title}&keyValue=${keyValue}`
				})
			},
		}
	};
</script>

<style lang="scss" scoped>
	@import "index.scss";

	.swiper {
		height: 175px;

		.itemimg {
			width: 100%;
			height: 100%;
		}

	}
</style>
